import languagesJson from './languages.json'

const initLanguages = () => {
    let languages = [];
    languagesJson.forEach(el => {
        let obj = el;
        obj.flag = require('../images/flags/' + el.flag).default;
        languages.push(obj);
    });
    return languages;
}

const getLanguage = (opposite = false) => {
    let siteContent;
    let lang = window.sessionStorage.getItem('language');

    if(lang === null) {
        window.sessionStorage.setItem('language', 'en');
        siteContent = require('./content_en.json');
    }else {
        if(opposite === true) {
            if(lang === 'en')
                lang = 'es';
            else
                lang = 'en';
        }
        siteContent = require('./content_' + lang + '.json')
    }
    return siteContent;
}

const languages = initLanguages();
const siteContent = getLanguage();

export { languages, siteContent };