import React from 'react';
import { Row, Col } from 'react-bootstrap';

import { siteContent } from '../../resources/content/content_setup';

const Menu = () => {
    return (
        <div className='page-container'>
                <div>
                    <h1>{ siteContent['menu']['title'] }</h1>

                    {
                        siteContent['menu']['menus'].map(el => 
                            <Row>
                                <Col>
                                    <h2>
                                        {
                                            el.title
                                        }
                                    </h2>

                                    {
                                        el['products'].map(prod => 
                                            <Row>
                                                <Col>
                                                    <p>{ prod['name'] }</p>
                                                </Col>
                                                <Col>
                                                    <p>{ prod['price'] }</p>
                                                </Col>
                                            </Row>
                                        )
                                    }
                                </Col>
                            </Row>
                        )
                    }
                </div>
        </div>
    )
}

export default Menu;
