import React from 'react';
import { Link as RLink } from "react-router-dom";
import {
    Events,
    scrollSpy,
    scroller
} from "react-scroll";

import CustomList from '../common/CustomList';
import { siteContent } from '../../resources/content/content_setup';

const Navbar = () => {
    const items = [
        {
            route: '/',
            name: siteContent['home']['title'],
            jsx:
                <RLink to='/'>
                    { siteContent['home']['title'] }
                </RLink>
        },
        {
            route: '/menu',
            name: siteContent['menu']['title'],
            jsx:
                <RLink to='/menu'>
                    { siteContent['menu']['title'] }
                </RLink>
        },
        {
            route: '/about',
            name: siteContent['about']['title'],
            jsx:
                <RLink to='/about'>
                    { siteContent['about']['title'] }
                </RLink>
        },
        {
            route: '/contact',
            name: siteContent['contact']['title'],
            jsx:
                <RLink to='/contact' onClick={() => scrollTo()}>
                    { siteContent['contact']['title'] }
                </RLink>
        },
    ];

    const scrollTo = (offset = -170, avoidHiding = false) => {
        /*if(this.props.isTransitionMenu === true && avoidHiding === false) {
            this.props.hideMenu()
        }else {
            if(this.props.isTransitionMenu === true)
                return
        }*/

        scroller.scrollTo('main', {
            duration: 300,
            smooth: true,
            spy: true,
            offset: offset
        })
    };

    return (
        <CustomList items={items} listOrientation={'h'} />
    )
}

export default Navbar
