import React, { useEffect, useState } from 'react';

import { languages } from '../../resources/content/content_setup';

const LanguageSelector = () => {
    const [selectedLanguage, setLanguageFlag] = useState(require('../../resources/images/flags/uk_flag.jpg').default);

    useEffect(() => {
        
    }, [])

    const langSelect = (lang) => {
        window.sessionStorage.setItem('language', lang.lang);
        window.location.reload(false);
    }

    return (
        <div id='language-selector'>
            <img src={selectedLanguage} />
            
            <div className='dropdown'>
                {
                    languages.map(el =>
                        <img src={el.flag} onClick={() => langSelect(el)} />
                )
            }
            </div>
        </div>
    )
}

export default LanguageSelector
