import React from 'react'

import Navbar from './Navbar';
import LanguageSelector from './LanguageSelector';

const TopBar = () => {

    

    return (
        <div id='topbar' className='prevent-text-selection'>
            <div className='page-container'>
                <div>
                    <Navbar />
                </div>
            </div>

            <LanguageSelector />
        </div>
    )
}

export default TopBar
